body{background-color:#FAFAFA}.header{max-width:60em;margin:0.5em auto 0 auto;display:flex}.header__title{margin:0}.header__title .header__subtitle{display:block}.main__nav{float:right;width:60%;margin-left:auto;display:flex;align-items:center;margin-right:0}.main__nav .menu__main{display:flex;list-style-type:none;width:100%;margin:0;padding:0;justify-content:space-around}@media screen and (max-width: 650px){.header{display:block;padding:0.5em}.header__title{font-size:1em;display:flex;justify-content:space-between}.header__title .header__subtitle{display:inline-block;font-size:1em}.main__nav{float:none;width:100%;margin:1em 0}}.cards{display:flex;margin:0 auto;max-width:100%;padding:1em 0;width:60em;flex-flow:row wrap}.card{box-sizing:border-box;flex:0 0 33.3333%;margin:0 0 1em 0;width:33.3333%}.card__link{background-color:#FFF;box-sizing:border-box;display:block;margin:0 1em;padding:1em;border:1px solid #eee;transition:box-shadow 200ms linear}.card__link:hover{box-shadow:2px 2px 8px rgba(0,0,0,0.3)}@media screen and (max-width: 650px){.card{flex:0 0 100%;width:100%}}.projects,.single-project{position:relative}.single-project{display:none;margin:0 auto;max-width:100%;width:60em;left:200px;opacity:0;padding:1em;box-sizing:border-box}
